<template>
  <main class="content container">
    <div v-if="productLoading"> </div>
    <div v-else-if="!productData">    </div>
    <div v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              {{ category.title }}
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              {{ product.title }}
            </a>
          </li>
        </ul>
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img width="570" height="570" :src="product.image" :alt="product.title" />
          </div>
        </div>

        <div class="item__info">
          <span class="item__code">: {{ product.id }}</span>
          <h2 class="item__title">
            {{ product.title }}
          </h2>
          <div class="item__form">
            <form class="form" action="#" method="POST" @submit.prevent="addToCart">
              <b class="item__price"> {{ product.price | numberFormat }}  </b>

              <fieldset class="form__block">
                <legend class="form__legend">:</legend>
                <ul class="colors">
                  <ProductColor
                    v-for="(item, index) in colors"
                    :key="index"
                    :color="item"
                    :currentColor.sync="productColor"
                  />
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">  :</legend>

                <ul class="sizes sizes--primery">
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input
                        class="sizes__radio sr-only"
                        type="radio"
                        name="sizes-item"
                        value="32"
                      />
                      <span class="sizes__value">
                        32gb
                      </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input
                        class="sizes__radio sr-only"
                        type="radio"
                        name="sizes-item"
                        value="64"
                      />
                      <span class="sizes__value">
                        64gb
                      </span>
                    </label>
                  </li>
                  <li class="sizes__item">
                    <label class="sizes__label">
                      <input
                        class="sizes__radio sr-only"
                        type="radio"
                        name="sizes-item"
                        value="128"
                        checked=""
                      />
                      <span class="sizes__value">
                        128gb
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <div class="item__row">
                <div class="form__counter">
                  <button
                    type="button"
                    aria-label="  "
                    :disabled="productAmount <= 1"
                    @click="minusAmount"
                  >
                    <svg width="12" height="12" fill="currentColor">
                      <use xlink:href="#icon-minus"></use>
                    </svg>
                  </button>

                  <input type="text" v-model.number="productAmount" name="count" />

                  <button type="button" aria-label="  " @click="addAmount">
                    <svg width="12" height="12" fill="currentColor">
                      <use xlink:href="#icon-plus"></use>
                    </svg>
                  </button>
                </div>

                <button class="button button--primery" type="submit" :disabled="productAddSending">
                   
                </button>
                <div v-show="productAdded">   </div>
                <div v-show="productAddSending">  </div>
              </div>
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a class="tabs__link tabs__link--current">
                
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                
              </a>
            </li>
            <li class="tabs__item">
              <a class="tabs__link" href="#">
                  
              </a>
            </li>
          </ul>

          <div class="item__content">
            <p>
               GPS, , BEIDOU Galileo  QZSS<br />
                <br />
                Bluetooth Smart, ANT+  Wi-Fi<br />
                <br />
            </p>

            <a href="#">
               
            </a>

            <h3> ?</h3>

            <p>
              Wahoo ELEMNT BOLT GPS   ,    
              ,    . Wahoo ELEMNT BOLT GPS
                  ANT+,     . 
                ,     .    
                   ,    
               .    , 
                  .     
                   .
            </p>

            <h3></h3>

            <p>
               Wahoo ELEMNT BOLT  .    
              74,6 x 47,3 x 22,1 .     .    
               .        
              56 .      ,      
                : ,   ,
                .. (     ).   
                  IPX7.  ,     ,  
                ( 30 )        1
              .
            </p>
          </div>
        </div>
      </section>
    </div>
  </main>
</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex'
import config from '@/config'
import goToPage from '@/helpers/goToPage'
import numberFormat from '@/helpers/numberFormat'
import ProductColor from '@/components/ProductColor.vue'

export default {
  name: 'ProductPage',
  components: {
    ProductColor,
  },
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingError: false,
      productAdded: false,
      productAddSending: false,
      productColor: null,
    }
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      const image = this.productData.image.file.url
      const product = this.productData
      product.image = image
      return product
    },
    category() {
      return this.productData.category
    },
    colors() {
      return this.productData.colors
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    goToPage,
    addToCart() {
      this.productAdded = false
      this.productAddSending = true
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      }).then(() => {
        this.productAdded = true
        this.productAddSending = false
      })
    },
    addAmount() {
      this.productAmount += 1
    },
    minusAmount() {
      this.productAmount -= 1
    },
    loadProduct() {
      this.productLoading = true
      this.productLoadingError = false
      axios
        .get(`${config}/api/products/${this.$route.params.id}`)
        .then((response) => {
          this.productData = response.data
          this.productColor = this.productData.colors.find((element, index) => index === 0)
        })
        .catch(() => {
          this.productLoadingError = true
        })
        .then(() => {
          this.productLoading = false
        })
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct()
      },
      immediate: true,
    },
  },
}
</script>
